import request from '@/utils/request'
import Qs from 'qs';
export function imgPage(data) {
	return request({
		url: 'query/img/imgPage',
		method: 'post',
		data
	})
}

export function musicQuery() {
	return request({
		url: "query/Music/all",
		method: "post"
	})
}

